.news{
  &-scrollbar{
    display: none;
  }
  &-slider{
    &__wrapper{
      align-items: stretch;
    }
    &__item{
      width: max-content;
      height: auto;
    }
  }
  &-item{
    height: 100%;
    width: 480px;
    &__wrapper{
      height: 100%;

      border: 1px solid #ECEDEF;
      border-radius: 20px;
      overflow: hidden;

      display: flex;
      flex-direction: column;
    }
    &__header{
      position: relative;
      width: 100%;
      height: 300px;

      flex-shrink: 0;

      padding: 16px;
    }
    &__img{
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      img{
        width: 100%;
        height: 100%;

        object-fit: cover;
        object-position: center center;

        border-radius: 20px;
      }
    }
    &__tags{
      position: relative;
      z-index: 2;

      margin: -5px;

      display: flex;
      flex-wrap: wrap;
    }

    &__tag{
      padding: 6px 14px;

      background: #FFFFFF;
      border-radius: 20px;

      margin: 5px;
      &-text{
        font-family: 'Inter', sans-serif;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;

        color: #212121;
      }
    }

    &__content{
      padding: 20px 16px 16px 16px;

      display: flex;
      flex-direction: column;

      height: 100%;
    }

    &__title{
      font-family: 'Inter', sans-serif;
      font-weight: 600;
      font-size: 24px;
      line-height: 89.02%;

      color: $textColorBlack;
    }

    &__text{
      margin: 10px 0 0 0;

      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: #464646;
    }

    &__footer{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 10px;

      margin: auto 0 0 0;
      padding: 30px 0 0 0;
    }

    &__button{
      height: 47px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .news-scrollbar{
    display: block;
  }
}

@media screen and (max-width: 600px) {
  .news{
    &-item{
      width: calc(100vw - 32px);
      &__header{
        height: 225px;
      }
      &__content{
        padding: 20px 10px;
      }
      &__button{
        height: 55px;
      }
      &__header{
        padding: 20px 10px;
      }
    }
  }
}