.integrate{
  overflow: hidden;
  &-slider{
    display: flex;
    align-items: center;
    &__wrapper{
      display: flex;
      align-items: center;

      & + &{
        margin-left: 40px;
      }
    }
    &__item{
      width: 215px;
      height: 100px;

      flex-shrink: 0;

      & + &{
        margin-left: 40px;
      }
    }
  }
  &-item{
    width: 100%;
    height: 100%;

    &__wrapper{
      padding: 10px;
      background: #FFFFFF;
      border-radius: 30px;
    }

    &__img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

@media screen and (max-width: 600px) {
  .integrate{
    &-slider{
      &__wrapper,&__item{
        & + &{
          margin-left: 10px;

        }
      }
      &__item{
        width: 126px;
        height: 66px;
      }
    }
    &-item{
      &__wrapper{
        border-radius: 20px;
        padding: 5px;
      }
    }
  }
}