.hero{
  &__wrapper{
    padding: 70px 0 0 0;
    height: 832px;
    display: grid;
    grid-template-columns: 763px auto;
    &-block{
      height: 100%;
    }
  }
  &__video{
    background: #D9D9D9;
    width: 100%;
    height: 100%;
    min-height: 310px;
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    &-description{
      margin: 10px 0 0 0;

      max-width: 580px;
    }
    &-footer{
      margin: 40px 0 0 0;

    }
  }

  &__title{
    font-size: 100px;
    line-height: 90%;

    color:$textColorBlack;
  }

  &__footer{
    display: grid;
    grid-template-columns: repeat(2,minmax(0,280px));

    gap: 10px;
  }

  &__button{
    height: 47px;
  }
}

@media screen and (max-width: 1700px) {
  .hero{
    &__wrapper{
      height: 570px;

      grid-template-columns: 508px auto;
    }
    &__title{
      font-size: 64px;

      max-width: 484px;
    }
  }
}


@media screen and (max-width: 1240px) {
  .hero{
    &__wrapper{
      height: auto;
      grid-template-columns: minmax(0,1fr);
      padding: 120px 0 50px 0;
      &-block{
        &:nth-child(1){
          grid-row-start: 2;
        }
        &:nth-child(2){
          grid-row-start: 1;
          display: none;
        }
      }
    }
    &__content{
      padding: 20px 0 0 0;
    }
  }
}

@media screen and (max-width: 1100px) {
  .hero__wrapper {
    padding: 80px 0 50px 0;
  }
}

@media screen and (max-width: 600px) {
  .hero{
    &__button{
      height: 55px;
    }
    &__title{
      font-size: 60px;
    }
    &__content{
      &-description{
        margin: 20px 0 0 0;
      }
      &-footer{
        margin: 30px 0 0 0;
      }
    }
    &__footer{
      grid-template-columns: minmax(0,1fr);
      gap: 10px;
    }
  }
}