.trust{
  overflow: hidden;
  &-slider{
    display: flex;
    align-items: center;
    &__wrapper{
      display: flex;
      align-items: center;
      & + &{
        margin-left: 20px;
      }
    }
    &__item {
      & + & {
        margin-left: 20px;
      }
    }
  }
  &-item{
    height: 100px;
    width: 100px;
    &__wrapper{
      padding: 10px;
      background: #FFFFFF;
      border-radius: 30px;
    }
    &__img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

@media screen and (max-width: 600px) {
  .trust{
    &-slider{
      &__wrapper,&__item{
        & + &{
          margin-left: 10px;
        }
      }
    }
    &-item{
      width: 66px;
      height: 66px;

      &__wrapper{
        padding: 8px;

        border-radius: 20px;
      }
    }
  }
}