.section{
  &-problem{
    &__content{
      border-top: 1px solid #E3EBF6;
      padding: 20px 0 0 0;
    }
  }
}

.problem{
  &__grid{
    display: grid;
    grid-template-columns: 360px 860px;
    gap: 140px;
    position: relative;

    &-button{
      position: absolute;
      right: 0;
      top: 4px;

      height: 47px;
    }
  }
  &-block{
    &__title{
      font-family: 'Inter', sans-serif;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;

      color: #212121;
    }
    &__button{
      margin: 40px 0 0 0;
      height: 47px;
      display: none;
    }
    &__content{
      margin: 23px 0 0 0;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: #212121;
      & + &{
        margin: 5px 0 0 0;
      }
    }
    &__list{
      margin: 20px 0 0 0;
    }
  }
  &-list{
    &_small{
      max-width: 332px;
    }
    &__item{
       & + &{
         margin: 20px 0 0 0;
       }

      display: flex;
      align-items: center;

      &::before{
        content: '';

        width: 24px;
        height: 24px;
        display: block;

        flex-shrink: 0;

        margin: 0 10px 0 0;
      }
      &_fail{
        &::before{
          background: url("../img/tariffs-fail.svg") center center/contain no-repeat;
        }
      }
      &_check{
        &::before{
          background: url("../img/tariffs-check.svg") center center/contain no-repeat;
        }
      }
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: -0.03em;

      color: #212121;
    }
  }
}

@media screen and (max-width: 1750px) {
  .problem{
    &__grid{
      &-button{
        display: none;
      }
    }
    &-block{
      &__button{
        &-tablet{
          display: block;
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .problem{
    &__grid{
      grid-template-columns: 390px 660px;
      gap: 60px;
    }
    &-block{
      &__text{
        max-width: 330px;
      }
    }
    &-list{
      &__item{
        & + &{
          margin: 14px 0 0 0;
        }
      }
    }
  }
}

@media screen and (max-width: 1160px){
  .problem{
    &__grid{
      grid-template-columns: minmax(0,1fr);

      gap: 30px;
      &-button{
        position: relative;
        left: 0;
        top: 0;


        display: flex;
      }
    }
    &-block{
      &__button{
        display: none;
      }
    }
  }
}