.tariffs {
  &-item {
    &__header{
      &-block{
        padding: 0 0 13px 0;

        border-bottom: 1px solid rgba(33, 33, 33, 0.32);
        & + &{
          padding: 13px 0 0 0;
        }
      }
    }
    &__wrapper{
      padding: 30px;
      background: #FFFFFF;
      border-radius: 20px;
    }
    &__content{
      margin:16px 0 0 0;
    }
    &__title{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 64px;
      line-height: 90%;

      color: #212121;
    }
    &__price{
      &-sign{
        font-family: 'Oswald', sans-serif;
        font-weight: 500;
        font-size: 56px;
      }
      &-month{
        font-size: 30px;
        color: #636363;
        &_top{
          transform: translateY(-20px);
          display: inline-block;
        }
      }
    }
    &__list{
      &-block{
        display: flex;
        align-items: center;
        justify-content: space-between;
        & + &{
          padding: 10px 0 0 0;
        }
        &::after{
          content: '';
          width: 24px;
          height: 24px;

          flex-shrink: 0 ;
          display: block;

          margin: 0 0 0 10px;

          background-repeat: no-repeat;
          background-position: center center;
          background-size: contain;
        }
        &_check{
          &::after{
            background-image: url("../img/tariffs-check.svg");
          }
        }
        &_fail{
          &::after{
            background-image: url("../img/tariffs-fail.svg");
          }
        }
      }
      &-text{
        font-family: 'Inter', sans-serif;
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: -0.03em;

        color: #212121;
      }
    }
    &__button{
      height: 47px;
    }
    &__footer{
      margin: 20px 0 0 0;
    }
  }
}

@media screen and (max-width: 1300px) {
  .tariffs{
    &-item{
      &__header{
        &-block{
          padding: 0 0 18px 0;
          & + &{
            padding: 6px 0 0 0;
          }
        }
      }
      &__wrapper{
        padding: 20px;
      }
      &__content{
        margin: 30px 0 0 0;
      }
      &__title{
        font-size: 57px;
      }
      &__price{
        &-month{
          font-size: 25px;

          transform: translateY(0);
        }
        &-sign{
          font-size: 49px;
        }
      }
      &__list{
        &-text{
          font-size: 16px;
          line-height: 19px;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .tariffs{
    &-item{
      &__header{
        &-block{
          padding: 0 0 14px 0;
          & + &{
            padding: 10px 0 4px 0;
          }
        }
      }
      &__content{
        margin: 20px 0 0 0;
      }
      &__title{
        font-size: 36px;
      }
      &__list{
        &-text{
          font-size: 18px;
          line-height: 22px;
        }
      }
      &__button{
        height: 55px;
      }
      &__price{
        &-sign{
          font-size: 30px;
        }
        &-month{
          font-size: 30px;
        }
      }
    }
  }
}