.section{
  &-indicators{
    &__content{
      margin: 54px 0 0 0;
    }
  }
}

.indicators{
  &-item{
    height: 100%;
    &__wrapper{
      padding: 20px;
      height: 100%;

      background: #F8FAFF;
      border-radius: 20px;

      display: flex;
      flex-direction: column;
    }
    &__tags{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #4B4B4B;

      padding: 6px 14px;
      background: #FFFFFF;
      border-radius: 20px;

      width: max-content;
    }
    &__content{
      margin: auto 0 0 0;
    }
    &__number{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 128px;
      line-height: 90%;
      color: #3F9D83;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 500;
      font-size: 20px;
      line-height: 20px;

      color: #4B4B4B;

      max-width: 215px;
    }
  }

  &__grid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    grid-auto-rows: 305px;
    gap: 40px;
  }
}

@media screen and (max-width: 1600px) {
  .section{
    &-indicators{
      &__content{
        margin: 70px 0 0 0;
      }
    }
  }
  .indicators{
    &__grid{
      gap: 26px;
      grid-auto-rows: 205px;
    }
    &-item{
      &__wrapper{
        padding: 13px;
      }
      &__number{
        font-size: 80px;
      }
      &__tags{
        padding: 4px 10px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .indicators{
    &__grid{
      width: max-content;
      grid-template-columns: repeat(4,minmax(0,320px));
      gap: 25px;
    }
  }
}

@media screen and (max-width: 600px) {
  .indicators{
    &__grid{
      grid-auto-rows: 230px;
      gap: 15px;
    }
    &-item{
      &__wrapper{
        padding: 20px;
      }
      &__tags{
        font-size: 12px;
        line-height: 15px;

        background: linear-gradient(91.03deg, #48B798 0.92%, #4D9DC6 141.46%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }
      &__number{
        font-size: 64px;
      }

    }
  }
}