.card{
  width: 440px;
  height: 600px;
  overflow: hidden;

  &__front,&__back{
    height: 100%;
    width: 100%;

    border-radius: 20px;
  }
  &__front{
    background: #FFFFFF;

    padding: 35px 30px 30px 30px;
  }
  &__title{
    font-family: 'Bebas Neue Cyrillic', sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 90%;

    color: $textColorBlack;

    &_white{
      color:#FFFFFF
    }
  }
  &__content{
    &_big{
      margin: 34px 0 0 0;
    }
  }
  &__back{
    padding: 45px 30px 30px 30px;
    background: $blackColor;
  }

  &__text{
    margin: 20px 0 0 0;
    p{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: rgba(#FFFFFF,.7);
    }
    h4{
      font-family: 'Inter', sans-serif;
      font-weight: 500;
      font-size: 18px;
      line-height: 112%;

      color: #FFFFFF;
    }
    p + h4{
      margin: 20px 0 0 0;
    }
    h4 + p{
      margin: 10px 0 0 0;
    }
    p + p{
      margin: 20px 0 0 0;
    }
  }

  &-list{
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__title{
      font-family: 'Inter', sans-serif;
      font-weight: 600;
      font-size: 30px;
      line-height: 36px;
      letter-spacing: -0.03em;

      color: $greenColor;
    }
    &__content{
      margin: 15px 0 0 0;
    }
    &__item{
      & + &{
        margin: 10px 0 0 0;
      }
    }

    &__button{
      width: 107px;
      height: 30px;

      background: #F8FAFF;
      border-radius: 15px;

      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;

      color: $blueColor;
      &::before{
        content: '';
        width: 20px;
        height: 20px;

        flex-shrink: 0;

        display: block;

        background: url("../img/card-plus.svg") center center/contain no-repeat;

        margin: 0 0 0 3px;
      }
    }
  }
  &-checkbox{
    display: flex;
    align-items: center;
    justify-content: space-between;

    background: #F8FAFF;
    border-radius: 15px;

    padding: 10px 20px;
    &__item{
      display: none;
    }
    &__item:checked ~ &__checkmark::before{
      left: 50%;
      background: $greenColor;
      border-color:$greenColor ;
    }
    &__item:checked ~ &__label::before{
      color:$greenColor;
    }
    &__label{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: $deepBlueColor;

      display: flex;
      align-items: center;
      &::before{
        content: '\e901';
        font-family: 'iconFont' !important;
        font-size: 14px;
        color: #334155;
        display: block;

        margin: 0 6px 0 0;

        transition: .3s color ;
      }
    }
    &__checkmark{
      width: 32px;
      height: 18px;

      background: #E3EBF6;
      border-radius: 20px;
      display: block;

      position: relative;
      &::before{
        content: '';
        position: absolute;
        left: 0;
        width: 18px;
        height: 18px;

        background: #FFFFFF;

        border: 1px solid #E3EBF6;

        border-radius: 50%;

        transition: .3s left linear;
      }
    }
  }
}

@media screen and (max-width: 600px) {
 .card{
   width: 330px;
   height: 535px;

   &__front{
     padding: 20px;
   }
   &__title{
     font-size: 30px;
   }
   &__back{
     padding: 20px;
   }
   &__text{
     margin: 10px 0 0 0;
     p + h4{
       margin: 15px 0 0 0;
     }
     h4 + p{
       margin: 6px 0 0 0;
     }
     p + p{
       margin: 15px 0 0 0;
     }
   }
 }
}