.feature{
  width: 822px;
  height: 710px;
  &-slider{
    &__item{
      width: max-content;
    }
  }
  &-item{
    &__wrapper{
      display: flex;
      max-width: 560px;
      &::before{
        content: '';
        width: 24px;
        height: 24px;
        display: block;

        background: url("../img/question.svg") center center/contain no-repeat;

        flex-shrink: 0;

        margin: 0 10px 0 0;
      }
    }
    &__title{
      font-family: 'Inter', sans-serif;
      font-weight: 500;
      font-size: 18px;
      line-height: 105.02%;

      color: $textColorBlack;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: $textColorBlack;

      margin: 10px 0 0 0;
    }
    &__content{
    }
  }
  &__front{
    padding: 30px;

    border: 1px solid #ECEDEF;
    border-radius: 30px;

    height: 100%;
    &-btn{
      width: 200px;
      height: 47px;

      margin: 0 0 0 40px;

      flex-shrink: 0;
    }
  }
  &__img{
    width: 100%;
    height: 470px;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: cover;

      background: #D9D9D9;
    }
  }
  &__content{
    margin: 35px 0 0 0;
  }
  &__title{
    font-family: 'Bebas Neue Cyrillic', sans-serif;
    font-weight: 400;
    font-size: 60px;
    line-height: 90%;

    color: $textColorBlack;
  }
  &__footer{
    margin: 30px 0 0 0;

    display: flex;
    align-items: center;
    justify-content: space-between;
    &-text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;

      color: #464646;
    }
  }
  &__back{
    border: 1px solid #ECEDEF;
    border-radius: 30px;

    padding: 30px;

    height: 100%;

    display: flex;
    flex-direction: column;

    &-footer{
      margin: auto 0 0 auto;
    }
    &-btn{
      width: 125px;
      height: 47px;
    }
  }

  &__question{
    &-title{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 30px;
      line-height: 90%;

      color: $textColorBlack;
    }
    &-content{
      margin: 30px 0 0 0;
    }
    &-item {
       & + &{
         margin: 30px 0 0 0;
       }
    }
  }
}

@media screen and (max-width: 1200px) {
  .feature{
    width: 684px;
    height: 733px;
    &__front,&__back{
      padding: 20px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .feature{
    &:hover .flip-card__front {
      transform: perspective(800px) rotateY(0deg);
    }
    &:hover .flip-card__back {
      display: none;
      transform: perspective(800px) rotateY(0deg);
    }
  }
}

@media screen and (max-width: 750px) {
  .feature{
    width: calc(100vw - 32px);
    height: 502px;
    &__img{
      height: 234px;
    }
    &__front{
      &-btn{
        margin: 20px 0 0 0;
        width: 100%;
        height: 55px;
      }

    }
    &__content{
      margin: 20px 0 0 0;
    }
    &__title{
      font-size: 26px;
      line-height: 90%;
    }
    &__footer{
      margin: 15px 0 0 0;
      flex-direction: column;
      &-text{
        font-size: 16px;
        line-height: 19px;
      }
    }
  }
}