.review{
  &-slider{
    &__wrapper{
      align-items: stretch;
    }
    &__item{
      width: max-content;
      height:auto;
    }
  }
  &-item{
    height: 100%;
    &__wrapper{
      height: 100%;
      padding: 30px;
      background: #FFFFFF;
      border-radius: 20px;

      width: 440px;
    }
    &__header{
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      &-block_img{
        flex-shrink: 0;
      }
      &-block + &-block{
        margin: 0 0 0 18px;
      }
      &::after{
        content: '';
        width: 60px;
        height: 40px;

        display: block;

        margin: 0 0 6px auto;

        background: url("../img/quote.svg") center right/contain no-repeat;

        flex: 100%;
      }
    }
    &__img{
      width: 124px;
      height: 124px;

      flex-shrink: 0;

      display: block;

      object-fit: cover;
      object-position: center center;


      border-radius: 50%;
    }
    &__name{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 34px;
      line-height: 90%;

      color: $blackColor;
    }
    &__product{
      margin: 7px 0 0 0;

      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 26px;
      line-height: 90%;

      color: #636363;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;

      color: #464646;

      transition: .3s height;
      &.hidden{
        overflow: hidden;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .review{
    &-item{
      &__wrapper{
        padding: 35px 40px;

        width: 460px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
 .review{
   &-item{
     &__header{
       &-block + &-block{
         margin: 0 0 0 23px;
       }
     }
     &__wrapper{
       width: calc(100vw - 32px);

       padding: 20px;
     }
     &__img{
       width: 83px;
       height: 83px;
     }
     &__name{
       font-size: 30px;
     }
     &__category{
       font-size: 18px;
     }
     &__content{
       &::before{
         width: 27px;
         height: 27px;
       }
     }
     &__text{
       margin: 10px 0 0 0;
     }
   }
 }
}