.ecosystem{
  &-slider{
    .swiper-wrapper{
      align-items: stretch;
    }
    &__item{
      width: 460px;
      height: auto;
    }
  }
  &-item{
    width: 100%;
    height: 100%;
    &__wrapper{
      height: 100%;

      padding:20px;

      background: #FFFFFF;
      border-radius: 20px;

      display: flex;
      flex-direction: column;
    }
    &__icon{
      width: 80px;
      height: 80px;
      &-img{
        width: 100%;
        height: 100%;

        border-radius: 50%;

        object-fit: cover;
      }
    }
    &__content{
      margin:30px 0 0 0;
    }
    &__title{
      font-family: 'Inter', sans-serif;
      font-weight: 600;
      font-size: 40px;
      line-height: 89.02%;

      color: #212121;

      display: flex;
      align-items: center;

      min-height: 105px;
    }
    &__description{
      margin:10px 0 0 0;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 145%;

      color: #464646;
    }
    &__footer{
      margin:auto 0 0 0;
      padding: 30px 0 0 0;
    }

    &__button{
      height: 47px;
    }
  }
}

@media screen and (max-width: 600px) {
  .ecosystem{
    &-slider{
      &__item{
        width: calc(100vw - 32px);
      }
    }
    &-item{
      &__icon{
        width: 58px;
        height: 58px;
      }
      &__content{
        margin: 0;
      }
      &__title{
        font-size: 30px;
      }
      &__description{
        margin: 0;
      }
      &__text{
        font-size: 16px;
      }
      &__footer{
        padding: 15px 0 0 0;
      }
      &__button{
        height: 55px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .section{
    &-ecosystem{
      &__title{
        max-width: 250px;
      }
    }
  }
}