body {
  color: #212121;
}

.container {
  padding: 0 100px;
  max-width: 1920px;
  margin: 0 auto;
}

.container-left {
  padding-right: 0;
}

.title {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
}

.title_tablet {
  display: none;
}

.parallax.transition {
  transition: .5s all linear;
}

.button {
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
}

.button-green {
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #FFFFFF;
  background: #3F9D83;
  border-radius: 30px;
  border: 1px solid #3F9D83;
  transition: 0.3s color cubic-bezier(0.215, 0.61, 0.355, 1), 0.3s background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-green:hover {
  color: #3F9D83;
  background-color: #fff;
}

.button-white {
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #3F9D83;
  border: 1px solid #3F9D83;
  border-radius: 30px;
  transition: 0.3s color cubic-bezier(0.215, 0.61, 0.355, 1), 0.3s background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-white:hover {
  color: #fff;
  background-color: #3F9D83;
}

.button-black {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #FFFFFF;
  background: #3E3E3E;
  border-radius: 40px;
}

.button-arrow {
  display: flex;
  align-items: center;
  width: max-content;
}

.button-arrow:hover .button-arrow__icon::before {
  transform: scale(0);
}

.button-arrow:hover .button-arrow__icon svg path {
  fill: #3E3E3E;
}

.button-arrow:hover .button-arrow__icon-green svg path {
  fill: #3F9D83;
}

.button-arrow:hover .button-arrow__text {
  color: #3E3E3E;
  background-color: transparent;
}

.button-arrow:hover .button-arrow__text-green {
  color: #3F9D83;
}

.button-arrow__text {
  height: 47px;
  width: 255px;
  border-radius: 30px;
  border: 1px solid #3E3E3E;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #fff;
  background-color: #3E3E3E;
  transition: 0.3s color cubic-bezier(0.215, 0.61, 0.355, 1), 0.3s background-color cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-arrow__text-green {
  border-color: #3F9D83;
  background-color: #3F9D83;
}

.button-arrow__icon {
  width: 47px;
  height: 47px;
  border-radius: 50%;
  border: 1px solid #3E3E3E;
  flex-shrink: 0;
  margin: 0 0 0 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button-arrow__icon svg {
  position: relative;
  z-index: 2;
}

.button-arrow__icon svg path {
  fill: #FFFFFF;
  transition: 0.3s fill cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-arrow__icon::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #3E3E3E;
  border-radius: 50%;
  transform: scale(1);
  transition: 0.3s transform cubic-bezier(0.215, 0.61, 0.355, 1);
}

.button-arrow__icon-green {
  border-color: #3F9D83;
}

.button-arrow__icon-green svg path {
  fill: #fff;
}

.button-arrow__icon-green::before {
  background-color: #3F9D83;
}

.icon {
  width: 100%;
  height: 100%;
}

.slider-scrollbar {
  position: relative;
  background: transparent;
  height: 3px;
  margin: 40px 0 0 0;
}

.slider-scrollbar .swiper-scrollbar-drag {
  height: 3px;
}

.slider-scrollbar_white {
  border-bottom: 1px solid #444343;
}

.slider-scrollbar_white .swiper-scrollbar-drag {
  background: #fff;
}

.slider-scrollbar_black {
  border-bottom: 1px solid #B5B5B5;
}

.slider-scrollbar_black .swiper-scrollbar-drag {
  background: #212121;
}

.slider__arrow {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 66px));
  grid-auto-rows: 66px;
  gap: 25px;
}

.slider__arrow-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.slider__arrow-btn_white {
  border: 1px solid #fff;
  position: relative;
}

.slider__arrow-btn_white::before {
  content: '\e900';
  font-family: 'iconFont' !important;
  font-size: 22px;
  color: #fff;
}

.slider__arrow-btn_white.swiper-button-disabled {
  border-color: #BCBCBC;
}

.slider__arrow-btn_white.swiper-button-disabled::before {
  color: #BCBCBC;
}

.slider__arrow-btn_black {
  border: 1px solid #212121;
  position: relative;
}

.slider__arrow-btn_black::before {
  content: '\e900';
  font-family: 'iconFont' !important;
  font-size: 22px;
  color: #212121;
}

.slider__arrow-btn_black.swiper-button-disabled {
  border-color: #B5B5B5;
}

.slider__arrow-btn_black.swiper-button-disabled::before {
  color: #B5B5B5;
}

.slider__arrow-prev {
  transform: rotate(180deg);
}

.flip-card {
  position: relative;
}

.flip-card__front, .flip-card__back {
  width: 100%;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  position: absolute;
  transition: transform .6s linear;
}

.flip-card__front {
  transform: perspective(800px) rotateY(0deg);
}

.flip-card__back {
  transform: perspective(800px) rotateY(180deg);
}

.flip-card:hover .flip-card__front {
  transform: perspective(800px) rotateY(-180deg);
}

.flip-card:hover .flip-card__back {
  transform: perspective(800px) rotateY(0deg);
}

.expanded-block {
  overflow: hidden;
  transition: .3s height;
}

.expanded-link {
  display: block;
  width: 47px;
  height: 47px;
  margin: 13px auto 0 auto;
  background: url("../img/expanded-arrow.svg") center center/contain no-repeat;
  transition: .3s transform;
}

.expanded-link:hover {
  cursor: pointer;
}

.expanded-link.active {
  transform: rotate(180deg);
}

@media screen and (min-width: 1920px) {
  .container-left {
    width: 100%;
    max-width: 100%;
    padding-left: calc(50% - 860px);
  }
}

@media screen and (max-width: 1600px) {
  .container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 1300px) and (min-width: 600px) {
  .title_tablet {
    display: inline-block;
  }
}

@media screen and (min-width: 1100px) {
  html,
  body {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
  body {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 1200px) {
  .slider__arrow {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .slider-scrollbar {
    margin: 30px 0 0 0;
  }
}

@media screen and (max-width: 600px) {
  .container {
    padding: 0 16px;
  }
  .container-full-m {
    padding: 0;
  }
  .button {
    height: 55px;
  }
  .button-arrow__text {
    height: 55px;
    border-radius: 40px;
  }
  .button-arrow__icon {
    height: 55px;
    width: 55px;
  }
  .button-white {
    border-radius: 40px;
  }
  .button-green {
    border-radius: 40px;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  background-color: #fff;
  transition: .5s transform linear;
}

.header.hidden {
  transform: translateY(-100%);
}

.header__info {
  display: flex;
  align-items: center;
}

.header__info-menu {
  padding: 0 15px 0 0;
  position: relative;
}

.header__info-menu::after {
  content: '';
  position: absolute;
  right: 0;
  top: -14px;
  width: 1px;
  height: 70px;
  background-color: #E3EBF6;
}

.header__info-logo {
  padding: 0 0 0 30px;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}

.header__wrapper-block_small {
  max-width: 210px;
  width: 100%;
}

.header__wrapper-block_burger {
  display: none;
}

.header__button {
  width: 178px;
}

.header-logo__wrapper {
  display: block;
  width: 160px;
  height: 45px;
}

.header-logo__img {
  width: 100%;
  height: 100%;
}

.header-logo__img_tablet, .header-logo__img_m {
  display: none;
}

.header-nav__list {
  display: flex;
  align-items: center;
}

.header-nav__item + .header-nav__item {
  margin: 0 0 0 50px;
}

.header-nav__link {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #212121;
  position: relative;
  display: block;
}

.header-nav__link::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -.15em;
  height: .05em;
  background: #212121;
  will-change: transform;
  transform-origin: 100% 100%;
  transform: scaleX(0);
  pointer-events: none;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: .5s;
}

.header-nav__link-wrapper {
  position: relative;
  display: block;
  overflow: hidden;
  height: 22px;
}

.header-nav__link-block {
  display: block;
  position: relative;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: .5s;
}

.header-nav__link-hover {
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(125%);
}

.header-nav__link:hover .header-nav__link-default {
  transform: translateY(-125%);
}

.header-nav__link:hover .header-nav__link-hover {
  transform: translateY(0);
}

.header-nav__link:hover::after {
  transform-origin: 0 0;
  transform: scaleX(1);
}

.header-menu__icon {
  width: 40px;
  height: 40px;
}

.header-menu__icon:hover .light-green {
  fill: #3F9D83;
}

.header-menu__icon:hover .deep-green {
  fill: #48B798;
}

.header-menu__icon svg {
  width: 100%;
  height: 100%;
}

.header-menu__icon svg rect {
  transition: .3s fill linear;
}

.burger {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  flex-direction: column;
  width: 30px;
  height: 17px;
}

.burger__item {
  flex-shrink: 0;
  width: 100%;
  height: 2px;
  background-color: #1B1B1B;
  border-radius: 30px;
}

.burger__item:nth-child(2) {
  width: 25px;
}

.burger__item:nth-child(3) {
  width: 20px;
}

@media screen and (max-width: 1300px) {
  .header__info-menu {
    display: none;
  }
  .header__info-logo {
    padding: 0;
  }
  .header-logo__wrapper {
    width: 130px;
    height: 25px;
  }
  .header-logo__img {
    display: none;
  }
  .header-logo__img_tablet {
    display: block;
  }
  .header__wrapper {
    height: 80px;
    align-items: center;
    padding: 20px 0;
  }
  .header__wrapper-block_burger {
    display: block;
  }
  .header__wrapper-block_desktop {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .header {
    background: #161616;
  }
  .header__wrapper {
    align-items: flex-end;
  }
  .header-logo__img {
    display: none;
  }
  .header-logo__img_m {
    display: block;
  }
  .burger__item {
    background-color: #fff;
  }
}

.footer__wrapper {
  background: #1B1B1B;
  padding: 100px 0 30px 0;
}

.footer__row + .footer__row {
  margin: 125px 0 0 0;
}

.footer__title-text {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
  font-size: 125px;
  line-height: 90%;
  color: #FFFFFF;
}

.footer__title-text + .footer__title-text {
  margin: 5px 0 0 0;
}

.footer__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__list-text {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 90%;
  color: #FFFFFF;
}

.footer__list-link {
  position: relative;
  display: block;
  width: max-content;
}

.footer__list-link:hover::before {
  animation: link .75s forwards;
}

.footer__list-link::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -3px;
  height: 1px;
  background: #fff;
  will-change: transform;
  transform-origin: 0 0;
  transform: scaleX(1);
  -webkit-animation: none;
  animation: none;
  pointer-events: none;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: .5s;
}

.footer__grid {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.footer-form__wrapper {
