  font-weight: 300;
  font-size: 12px;
  line-height: 15px;
  color: #E3EBF6;
  max-width: 250px;
}

.footer-form__text a {
  text-decoration: underline;
}

.footer-form__button {
  width: 205px;
  height: 47px;
}

@media screen and (max-width: 1800px) {
