@keyframes link {
  0%, 0.1% {
    transform-origin: 100% 100%;
    transform: scaleX(1);
  }
  49.9% {
    transform-origin: 100% 100%;
    transform: scaleX(0);
  }
  50%, 50.1% {
    transform-origin: 0 0;
    transform: scaleX(0);
  }
  to {
    transform-origin: 0 0;
    transform: scaleX(1);
  }
}
