.section {
  &-header{
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__wrapper {
    padding: 100px 0 100px 0;

    background-color: #FFFFFF;

    &_big{
      padding-bottom: 350px;
    }
    &_bordered{
      border-radius: 50px 50px 0px 0px;
    }
    &_black{
      background: $blackColor;
    }
    &_purple{
      background: $purpleColor;
    }
    &_green{
      background: $greenColor;
    }
    &_deepblue{
      background: #162933;
    }
  }

  &__content{
    margin: 50px 0 0 0;
    &_big{
      margin: 72px 0 0 0;
    }
  }

  &__title{
    text-transform: uppercase;
    color:#fff;
    font-size: 96px;
    line-height: 90%;
    &_black{
      color:$textColorBlack;
    }
  }

  &__description {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;

    color: $textColorBlack;

    &_white{
      color:#fff;
    }
  }

  &__footer{
    margin: 45px 0 0 0;
  }
}

@media screen and (max-width: 1100px) {
  .section{
    &-statistics{
      background-color: $greenColor;
    }
    &-how{
      background-color: $greenColor;
    }
    &-tariffs{
      background-color:#162933;
    }
    &-indicators{
      background-color: #162933;
    }
    &-trust{
      background-color: $greenColor;
    }
    &-review{
      background-color: $greenColor;
    }
    &__wrapper{
      &_bordered{
        border-radius: 20px 20px 0px 0px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .section{
    &__wrapper{
      padding: 100px 0 ;
    }
  }
}

@media screen and (max-width: 1024px) {
  .section{

  }
}

@media screen and (max-width: 600px) {
  .section{
    &__content{
      margin: 30px 0 0 0;
    }
    &__wrapper{
      padding: 50px 0;
    }
    &__title{
      font-size: 48px;
    }
    &__description{
      font-size: 16px;
      line-height: 22.4px;
    }
  }
}