.footer{
  &__wrapper{
    background: $blackColor;

    padding: 100px 0 30px 0;
  }
  &__row{
    & + &{
      margin: 125px 0 0 0;
    }
  }
  &__title{
    &-text{
      font-family: 'Bebas Neue Cyrillic', sans-serif;
      font-weight: 400;
      font-size: 125px;
      line-height: 90%;

      color: #FFFFFF;
      & + &{
        margin: 5px 0 0 0;
      }
    }
  }
  &__list{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 90%;

      color: #FFFFFF;
    }
    &-link{
      position: relative;
      display: block;
      width: max-content;
      &:hover::before{
        animation: link .75s forwards;
      }
      &::before{
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: -3px;
        height: 1px;
        background: #fff;
        will-change: transform;
        transform-origin: 0 0;
        transform: scaleX(1);
        -webkit-animation: none;
        animation: none;
        pointer-events: none;
        transition-property: transform;
        transition-timing-function: cubic-bezier(.215,.61,.355,1);
        transition-duration: .5s;
      }
    }
  }
  &__grid{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &-form{
    &__wrapper{
      width: 720px;
    }
    &__input{
      & + &{
        margin: 25px 0 0 0;
      }
      &-item{
        width: 100%;
        height: 60px;

        background: #373737;
        border-radius: 10px;

        font-family: 'Inter', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        color: #E3EBF6;

        padding: 20px 22px;
      }
      &-textarea{
        resize: none;
        height: 240px;
      }
    }
    &__submit{
      margin: 17px 0 0 0;
      display: flex;
      justify-content: space-between;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 300;
      font-size: 12px;
      line-height: 15px;

      color: #E3EBF6;
      max-width: 250px;
      a{
        text-decoration: underline;
      }
    }
    &__button{
      width: 205px;
      height: 47px;
    }
  }
}

@media screen and (max-width: 1800px) {
  .footer{
    &__title{
      &-text{
        font-size: 84px;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer{
    &__list{
      flex-wrap: wrap;
      &-item{
        flex: 33%;
        flex-shrink: 0;
        height: 50px;
        display: flex;
        align-items: center;
        &:nth-child(1){
          order: 4;
        }
        &:nth-child(4){
          order: 1;
        }
        &:nth-child(3){
          order: 3;
          justify-content: flex-end;
        }
        &:nth-child(2){
          order: 2;
          justify-content: center;
        }
        &:nth-child(5){
          order: 5;
          justify-content: center;

        }
        &:nth-child(6){
          order: 6;
          justify-content: flex-end;
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer{
    &-form{
      &__wrapper{
        width: 530px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .footer{
    &__grid{
      flex-direction: column;
      &-item{
        & + &{
          margin: 26px 0 0 0;
        }
      }
    }
    &__wrapper{
      padding: 50px 0;
    }
    &__title{
      &-text{
        font-size: 48px;
        & + &{
          margin: 0;
        }
      }
    }
    &__row{
      & + &{
        margin: 65px 0 0 0;
      }
    }
    &__list{
      &-item{
        flex: 100%;
        &:nth-child(odd),&:nth-child(even){
          justify-content: flex-start;
        }
        &:nth-child(1){
          order: 7;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .footer{
    &__grid{
      &-item{
        width: 100%;
      }
    }
    &-form{
      &__wrapper{
        width: 100%;
      }
      &__input{
        & + &{
          margin: 15px 0 0 0;
        }
        &-item{
          height: 50px;
        }
        &-textarea{
          height: 100px;
        }
      }
      &__submit{
        margin: 30px 0 0 0;

        flex-direction: column-reverse;
        &-block{
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
      &__button{
        width: 100%;
        height: 55px;
      }
      &__text{
        text-align: center;

        margin: 15px 0 0 0;
      }
    }
  }
}